.ms-theme-light {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  background: var(--ms-backgroundColor);
  color: var(--ms-textColor);
  --ms-textColor: #333;
  --ms-backgroundColor: white;
  --ms-borderColor: #eaeaea;
  --ms-backgroundHoverColor: #eee;
  --ms-backgroundPressedColor: #ccc;
  --ms-backgroundDisabledColor: red;
  --ms-focusBorderColor: black;
  --ms-subTextColor: #888;
  --ms-textDisabledColor: red;
  --ms-iconColor: #106ebe;
  --ms-linkColor: red;
  --ms-linkHoverColor: red; }
  .ms-theme-light .ms-scheme-strong {
    -webkit-transition: background-color 0.1s linear;
    transition: background-color 0.1s linear;
    background: var(--ms-backgroundColor);
    color: var(--ms-textColor);
    --ms-textColor: #333;
    --ms-backgroundColor: #0078d4;
    --ms-borderColor: #eaeaea;
    --ms-backgroundHoverColor: #2b88d8;
    --ms-backgroundPressedColor: #71afe5;
    --ms-backgroundDisabledColor: red;
    --ms-focusBorderColor: #c7e0f4;
    --ms-subTextColor: #888;
    --ms-textColor: #fff;
    --ms-textDisabledColor: red;
    --ms-iconColor: #fff;
    --ms-linkColor: red;
    --ms-linkHoverColor: red; }
  .ms-theme-light .ms-scheme-neutral {
    -webkit-transition: background-color 0.1s linear;
    transition: background-color 0.1s linear;
    background: var(--ms-backgroundColor);
    color: var(--ms-textColor);
    --ms-textColor: black;
    --ms-backgroundColor: #f4f4f4;
    --ms-borderColor: #f4f4f4;
    --ms-backgroundHoverColor: #eaeaea;
    --ms-backgroundPressedColor: #dadada;
    --ms-backgroundDisabledColor: red;
    --ms-focusBorderColor: black;
    --ms-subTextColor: #666;
    --ms-textDisabledColor: red;
    --ms-iconColor: #106ebe;
    --ms-linkColor: red;
    --ms-linkHoverColor: red; }

.ms-theme-dark {
  -webkit-transition: background-color 0.1s linear;
  transition: background-color 0.1s linear;
  background: var(--ms-backgroundColor);
  color: var(--ms-textColor);
  --ms-textColor: white;
  --ms-backgroundColor: #484644;
  --ms-borderColor: #585654;
