  --ms-linkColor: red;
  --ms-linkHoverColor: red; }
  .ms-theme-dark .ms-scheme-strong,
  .ms-theme-dark .ms-scheme-dark {
    -webkit-transition: background-color 0.1s linear;
    transition: background-color 0.1s linear;
    background: var(--ms-backgroundColor);
    color: var(--ms-textColor);
    --ms-textColor: white;
    --ms-backgroundColor: #383634;
    --ms-borderColor: #585654;
